export default function NewVote(props) {
    const { search } = props.location
    const searchParams = new URLSearchParams(search)
    const pURL = searchParams.get('url');
    const isventure = searchParams.get('venture')

    const { state, dispatch } = useWeb3();
    const { web3Instance, Account, maincontract, vaultcontract, daoOrg, orgcontract,  } = state;

    const [date, setdate] = useState('');
    const [title, settitle] = useState('');
    const [desc, setdesc] = useState('');
    const [id, setId] = useState(null);
    const [supportInput, setsupportInput] = useState();
    const [supportPercent, setsupportPercent] = useState();
    const [type, settype] = useState(1);
    const [to_address, setto_address] = useState('');
    const [erc20address, seterc20address] = useState('');
    const [valueAmount, setvalueAmount] = useState('');

    const [optionlist, setoptionlist] = useState(['Agree', 'Disagree']);
    const [optchecked, setoptchecked] = useState(false);
    const [walletTips, setWalletTips] = useState(false);
    const [resultDate, setresultDate] = useState('');
    const [mydate, setmydate] = useState(null);
    const [list, setList] = useState([]);

    const [ventureType, setVentureType] = useState(0)
    const [gplist, setGplist] = useState([])
    const [selectGP, setSelectGP] = useState(null)

    const [newGp, setNewGp] = useState()
    const [newVoteConfig, setNewVoteConfig] = useState({})

    const { TextArea } = Input;

    let { t } = useTranslation();
    const ManageFeeRef = useRef();

    const VentureTypes = [
        `${t('vote.Addtransfer')}`, `${t('vote.ApproveGp')}`, `${t('vote.KickGP')}`, `${t('vote.VoteSettlement')}`, t('vote.AddGP'), t('vote.TransferManageFee'), 
    ]

    useEffect(()=>{
        setId(Number(props.match.params.id));
    }, [props.match.params.id]);

    const checkBonus = (gpList)=>{
        const bonus_arr = gpList.map(a=>ethers.BigNumber.from(a))
        const v = bonus_arr.reduce(function(accumulator,currentValue,currentIndex,array){
            console.log(accumulator, currentValue)
            return accumulator.add(currentValue)
        });
        return v.toNumber() === 100
    }

    const handleClicktoVote = async () => {
        if (optchecked) {
            if (!erc20address) {
                dispatch({ type: 'MSGTYPE', payload: { msg: 'please select token' } });
                return 
            }
            if (valueAmount) {
                dispatch({ type: 'LOADINGTYPE', payload: 'Creating vote' });
                let dataobj = {
                    title,
                    desc,
                    vote_time: date,
                    min_require_num: supportInput,
                    support_require_percent: supportPercent,
                    choices: optionlist.join('|'),
                    tokenAmount: valueAmount,
                    tokenAddress: erc20address,
                    toAddress: to_address,
                }
                console.log("====dataobj", dataobj)

                const signer = web3Instance.getSigner(Account)
                try {
                    await api.vote.newWithdrawVote(daoOrg, signer, dataobj)
                    setdate('')
                    settitle('')
                    setdesc('')
                    setvalueAmount('')
                    setto_address('')
                    setsupportInput('')
                    setsupportPercent('')
                    setoptionlist(['', '', ''])
                    settype(1)
                    props.history.push(pURL)
                
                } catch (error) {
                    setWalletTips(true)
                    dispatch({ type: 'MSGTYPE', payload: { msg: `${(error.data && error.data.message) || 'created vote failed'}` } });

                } finally {
                    dispatch({ type: 'LOADINGTYPE', payload: null });

                }
            } else {
                setWalletTips(true)
                dispatch({ type: 'MSGTYPE', payload: { msg: 'Receiver\'s address & Amount is requierd' } });
            }
        } else if (isventure) {
            dispatch({ type: 'LOADINGTYPE', payload: t('vote.CreateNewVote') });
            let dataobj = {
                title,
                desc,
                vote_time: date,
                min_require_num: supportInput,
                support_require_percent: supportPercent,
                choices: optionlist.join('|')
            }
            const signer = web3Instance.getSigner(Account)
            // ventureType
            try {
                if (ventureType === 0) {
                    // TODO hardcode
                    const contract = await api.erc20.getContract(web3Instance, erc20address)
                    const deci = await contract.decimals()
                    dataobj.tokenAmount = ethers.utils.parseUnits(valueAmount, deci)
                    dataobj.tokenAddress = erc20address
                    dataobj.toAddress = to_address
                    await api.vote.newWithdrawVote(daoOrg, signer, dataobj)
                } else if (ventureType === 1) {
                    // approv GP
                    dataobj.approvTime = Math.abs(Math.round((approveDuration-(new Date().getTime()))/1000))
                    dataobj.gpAddress = selectGP
                    dataobj.callee = vaultcontract.address
                    await api.vote.newApproveGPVote(daoOrg, signer, dataobj)
                } else if (ventureType === 2) {
                    // kick GP
                    dataobj.gpAddress = selectGP
                    const gp_address = [], gp_bonus = []
                    gplist.forEach(l=>{
                        if (l.address !== selectGP) {
                            gp_address.push(l.address)
                            gp_bonus.push(l.bonus)
                        }
                    })
                    if (gp_bonus.length > 0 && !checkBonus(gp_bonus)) return

                    dataobj.gpList = gp_address
                    dataobj.gpBonus = gp_bonus
                    dataobj.newVoteConfig = newVoteConfig
                    await api.vote.newKickGPVote(daoOrg, signer, dataobj)
                } else if (ventureType === 3) {
                    // transfer to lockup period
                    await api.vote.newTransfrtPeriod(daoOrg, signer, dataobj)
                } else if (ventureType === 4) {
                    // add GP
                    if (!newGp || !newGp.address || !newGp.bonus) return
                    dataobj.newGP = newGp.address
                    const gp_address = gplist.map(l=>l.address)
                    const gp_bonus = gplist.map(l=>l.bonus)
                    gp_address.push(newGp.address)
                    gp_bonus.push(newGp.bonus)

                    if (!checkBonus(gp_bonus)) return

                    dataobj.gpList = gp_address
                    dataobj.gpBonus = gp_bonus
                    dataobj.newVoteConfig = newVoteConfig
                    await api.vote.newAddGPVote(daoOrg, signer, dataobj)
                } else if (ventureType === 5) {
                    const data = ManageFeeRef.current.data
                    Object.assign(dataobj, data)
                    await api.vote.newTransferManageFeeVote(daoOrg, signer, dataobj)
                }
                props.history.push(pURL)
            } catch (error) {
                console.error('create vote failed', error)
                setWalletTips(true)
                dispatch({ type: 'MSGTYPE', payload: { msg: `${(error.data && error.data.message) || 'created vote failed'}` } });
            } finally {
                dispatch({ type: 'LOADINGTYPE', payload: null });
            }

        } else {
            dispatch({ type: 'LOADINGTYPE', payload: t('vote.CreateNewVote') });
            const signer = web3Instance.getSigner(Account)
            let dataobj = {
                title,
                desc,
                vote_time: date,
                min_require_num: supportInput,
                support_require_percent: supportPercent,
                choices: optionlist.join('|')
            }
          
            try {
                const signer = web3Instance.getSigner(Account)
                await api.vote.newVote(daoOrg, signer, dataobj)

                setdate('')
                settitle('')
                setdesc('')
                setvalueAmount('')
                setto_address('')
                setsupportInput('')
                setsupportPercent('')
                setoptionlist(['', '', ''])
                settype(1)
                props.history.push(pURL)
            } catch (error) {
                console.error(error)
                setWalletTips(true)
                dispatch({ type: 'MSGTYPE', payload: { msg: `Create New Vote: ${(error.data && error.data.message) || 'failed'}` } });
            } finally {
                dispatch({ type: 'LOADINGTYPE', payload: null });
            }
        }
    }

    const disabledDate = (current) => {
        return current && current <= moment().startOf('day');
    }

    const range = (start, end) => {
        const result = [];
        for (let i = start; i < end; i++) {
            result.push(i);
        }
        return result;
    }

    const disabledDateTime = (dates) => {
        let hours = moment().hours(); //0~23
        let minutes = moment().minutes(); //0~59
        let seconds = moment().seconds(); //0~59
        if (dates && moment(dates[1]).date() === moment().date()) {
            return {
                disabledHours: () => range(0, hours),
                disabledMinutes: () => range(0, minutes),
                disabledSeconds: () => range(0, seconds + 1),
            };
        }
    }

    
    const handleInputChange = (e) => {
        const { name, value } = e.target;
        let str = `set${name}`
        eval(str)(value)
    }
    const nextStep = (type) => {
        const nowTime = Date.parse(new Date());
        if (type === 2 && date <= 0) {
            setWalletTips(true)
            dispatch({ type: 'MSGTYPE', payload: { msg: 'Please fill the correct time' } });
        } else {
            settype(type)
        }

    }

    const format = (dateTime) => {
        var time = new Date(dateTime);
        var y = time.getFullYear();
        var m = time.getMonth() + 1;
        var d = time.getDate();
        var h = time.getHours();
        var mm = time.getMinutes();
        var s = time.getSeconds();
        return y + '-' + (m < 10 ? '0' + m : m) + '-' + (d < 10 ? '0' + d : d) + ' ' + (h < 10 ? '0' + h : h) + ':' + (mm < 10 ? '0' + mm : mm) + ':' + (s < 10 ? '0' + s : s);
    }

    const handleChange = (value) => {
        setmydate(value)
        const nowTime = Date.parse(new Date())
        const dateTime = Date.parse(value._d)
        setdate(Math.round((dateTime - nowTime) / 1000))
        setresultDate(format(dateTime))
    }

    const handleActive = (e) => {
        let values = JSON.parse(e.target.checked)
        setoptchecked(values)
    };
    const removeDate = () => {
        setdate('')
    };
    const handleSelect = (value) => {
        seterc20address(value)
    }
    let { handleClose, showTips } = props;

    useEffect(async () => {
        if (!id || !maincontract) return
        if (!daoOrg || !vaultcontract) {
            try {
                const data = await maincontract.instanceMap(id);
                const daoOrg = await api.daoOrg.getContract(web3Instance, data.daoOrganization) 

                const templateConfigAddress = await daoOrg.templateConfig()
                const templateConfig = await api.template.getContract(web3Instance, templateConfigAddress)

                const vaultAddress = await templateConfig.labelComponent('VoteManager')
                const vaultcontract = await api.vault.getContract(web3Instance, vaultAddress)
                dispatch({type: 'SET_VAULT', payload: vaultcontract})

            } catch (error) {
                console.error('init dao failed in new-vote', error)
            }
            return
            
        } 

        const getToken = async (address)=>{
            try {
                const mockERC20 = await api.erc20.getContract(web3Instance, address)
                const symbol = await mockERC20.symbol()
                const deci = await mockERC20.decimals() 
                const balance = (await api.vault.getTokenBalance(vaultcontract, address)).toString()
                return { symbol, deci, address, balance }
    
            } catch (error) {
                console.error('[getToken] failed', error)
                return {}
            }
        }
       
        const getTokens = async()=>{
            let data = await api.vault.getTokenList(vaultcontract)

            if (!data || data.length === 0) {
                return
            }

            let reqs = []
            data.forEach(tokenAddress=>{
                reqs.push(getToken(tokenAddress))
            })
            const result = await Promise.all(reqs)
            let lst = []
            for (let i=0; i<result.length; i++) {
                let tdata = result[i]
                lst.push({
                    symbol: tdata.symbol,
                    address: tdata.address,
                    balance: tdata.balance,
                    balanceDisplay: getShortDisplay(ethers.utils.formatUnits(tdata.balance, tdata.deci))
                })
            }

            setList(lst)
            
        }
        getTokens()
        
    }, [maincontract, vaultcontract, daoOrg, id]);
    // useEffect(() => {
    //     if (apiState !== 'READY') return;
    //     dispatch({ type: 'LOAD_MAINCONTRACT' });
    // }, [apiState]);
    useEffect(()=>{
        if (!isventure || !orgcontract || !id) return
        const getGPlist = async ()=>{
            const {lst} = await api.org.getDaoMembersList(orgcontract, 0, 1000)
            setGplist(lst);
        }
        getGPlist()
    }, [id, orgcontract])

    const [approveDuration, setApproveDuration] = useState(null)
    const handleApproveDuration = v=>{
        const dateTime = Date.parse(v._d)
        setApproveDuration(dateTime)
    }

    const handleSetBonus = (e, index)=>{
        let newArray = [...gplist];
        const { name, value } = e.target;
        newArray[index].bonus = Number(value);
        setGplist(newArray)
    }
    const handleChangeVentureType = (v)=>{
        setVentureType(v)
        const gpList = [...gplist]
        gpList.forEach(g=>g.bonus=null)
        setGplist(gplist)
    }

    const VentureStep = (
        <Venture className='font-light'>
            <div className='line'>
                <div className='label'>{t('vote.select')}</div>
                <SelectShortBrdr onChange={(v)=>{handleChangeVentureType(v)}} key={ventureType} defaultValue={ventureType}>
                    {
                        VentureTypes.map((item, i)=>{
                            return (
                                <Select.Option value={i} key={i}>
                                    <div className='select-item'>{item} </div>
                                </Select.Option>
                            )
                        })
                    }
                </SelectShortBrdr>
                <div className='type-desc font-light'>
                    {
                        ventureType === 1 && 
                        <span>*{t('vote.ventureType1')}</span>
                    }
                    {
                        ventureType === 2 && 
                        <span>*{t('vote.ventureType2')}</span>
                    }
                    {
                        ventureType === 3 && 
                        <span>*{t('vote.ventureType3')}</span>
                    }
                </div>
            </div>
            {
                ventureType === 0 && (
                    <>
                    <div className="venture-line">
                        <div className="inputBrdr">
                            <SelectBrdr style={{ width: '100%' }} onChange={handleSelect} placeholder={t('vote.SelectOption')}>
                                {
                                    list.map((i) =>
                                        <Select.Option value={i.address} key={i.address}>
                                            <div style={{ display:'flex', alignItems:'center'}}>
                                                <div style={{ display:'flex', flexDirection:'column', flexGrow:1}}>
                                                    <div style={{lineHeight: '3.55rem',color: '#10164B', textTransform:'uppercase'}}>{i.symbol}</div>
                                                    <div style={{color: '#A6A6B7', lineHeight: '3.55rem'}}>{`${i.address.slice(0,5)}…${i.address.slice(-5)}`}</div>
                                                </div>
                                                <div style={{textAlign:'right', color: '#A6A6B7', paddingRight: '2rem'}}>{t('vote.Available')}: {i.balanceDisplay}</div>
                                            </div>
                                        </Select.Option>
                                    )
                                }
                            </SelectBrdr>
                        </div>
                    </div>
                    <div className='venture-line'>
                        <div className="line">
                            <div className='label'>{t('vote.newOwner')}</div>
                        </div>
                        <div className='inputBrdr'>
                            <Input
                                placeholder={t('vote.enter')}
                                name='to_address'
                                value={to_address}
                                onChange={handleInputChange}
                                autoComplete="off"/>
                        </div>
                    </div>
                    <div className='venture-line'>
                        <div className="line">
                         <div className='label'>{t('vote.fillAmount')}</div>
                        </div>
                        <div className='inputBrdr'>
                            <Input
                                placeholder={t('vote.enter')}
                                name='valueAmount'
                                type="number"
                                value={valueAmount}
                                onChange={handleInputChange}
                                autoComplete="off"/>
                        </div>
                    </div>
                    </>
                )
            }
            {
                ventureType === 1 && (
                    <>
                    <div className='venture-line'>
                        <div className="line">
                            <div className='label'>{t('vote.Duration')}</div>
                        </div>
                        <DatePicker
                            className="myDatePicker"
                            placeholder={t('vote.Duration')}
                            style={{ width: '100%' }}
                            format="YYYY-MM-DD HH:mm:ss"
                            allowClear={false}
                            disabledDate={disabledDate}
                            onChange={handleApproveDuration}
                            showTime={true}
                            showNow={false} />
                        <Estimate time={approveDuration} />
                    </div>
                    <div className='venture-line'>
                        <div className="line">
                            <div className='label'>{t('vote.ApproveGp')}</div>
                        </div>
                        <SelectBrdr onChange={(v)=>{setSelectGP(v)}}>
                            {/* TODO GP address */}
                            {
                                gplist.map((item, i)=>{
                                    return (
                                        <Select.Option value={item.address} key={i}>
                                            <div className='select-item'>{item.address} </div>
                                        </Select.Option>
                                    )
                                })
                            }
                        </SelectBrdr>
                    </div>
                    </>
                )
            }
            {
                ventureType === 2 && <div className='venture-line'>
                    <div className="line">
                        <div className='label'>{t('vote.GPAddress')}</div>
                    </div>

                    <SelectBrdr onChange={(v)=>{setSelectGP(v)}}>
                        {/* TODO GP address */}
                        {
                            gplist.map((item, i)=>{
                                return (
                                    <Select.Option value={item.address} key={i}>
                                        <div className='select-item'>{item.address} </div>
                                    </Select.Option>
                                )
                            })
                        }
                    </SelectBrdr>
                    {
                        selectGP && <GpInfo>
                            <div className="line top">
                                <div className='label'>{t('vote.GpInfo')}</div>
                            </div>
                            <ul>
                                {
                                    gplist.map((item, i)=>{
                                        if (item.address === selectGP) {
                                            return <></>
                                        } else {
                                            return <li>
                                                        <div className="line">
                                                            <div className="label lft"> <span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(item.address)}
                    
                                                            </div>
                    
                                                            <div className='inputBrdr rht'>
                                                                <div className="label">{t('vote.bonus')}</div>
                                                                <Input
                                                                    placeholder={t('vote.bonus')}
                                                                    type="number"
                                                                    onChange={(event) => handleSetBonus(event, i)}
                                                                    autoComplete="off"/>
                                                                <span className='suffix'>%</span>
                                                            </div>
                                                        </div>
                                                    </li>
                                        }
                                    })

                                }
                            </ul>
                        </GpInfo>
                    }
                    <VoteInfo>
                        <div className="line top">
                            <div className='label'>{t('vote.voteInfo')}</div>
                        </div>
                        <ul>
                            <li>
                                <div className="lft">
                                    <div className="line">
                                        <div className="label">{t('vote.minimumPassed')}</div>
                                    </div>
                                    <div className='inputBrdr rht'>
                                        <Input
                                            placeholder={t('vote.enter')}
                                            onChange={e=>{setNewVoteConfig({...newVoteConfig, minSurportRequire:Number(e.target.value)})}}
                                            autoComplete="off"/>
                                    </div>
                                </div>
                                <div className="rht">
                                    <div className="line">
                                        <div className="label">{t('vote.minimumVotes')}</div>
                                    </div>
                                    <div className='inputBrdr rht'>
                                        <Input
                                            placeholder={t('vote.enter')}
                                            onChange={e=>{setNewVoteConfig({...newVoteConfig, minRequire:Number(e.target.value)})}}
                                            autoComplete="off"/>
                                    </div>
                                </div>
                            </li>
                        </ul>
                    </VoteInfo>
                </div>
            }
            {
                ventureType === 3 && <div className='venture-line'>
                </div>
            }
            {
                ventureType === 4 && <>
                    <div className='venture-line'>
                        <div className="line">
                            <div className='label'>{t('vote.GPAddress')}</div>
                        </div>
                        <div className='inputBrdr rht'>
                            <Input
                                autoComplete="off"
                                onChange={e=>{setNewGp({...newGp, address:e.target.value})}}
                            />
                        </div>
                        {
                            newGp && newGp.address && <GpInfo>
                                <div className="line top">
                                    <div className='label'>{t('vote.GpInfo')}</div>
                                </div>
                                <ul>
                                    <li>
                                        <div className="line">
                                            <div className="label lft"><span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(newGp.address)}</div>
                                            <div className='inputBrdr rht'>
                                                <div className="label">{t('vote.bonus')}</div>
                                                <Input
                                                    type="number"
                                                    placeholder={t('vote.bonus')}
                                                    onChange={(e) => setNewGp({...newGp, bonus:Number(e.target.value)})}
                                                    autoComplete="off"/>
                                                <span className='suffix'>%</span>
                                            </div>
                                        </div>
                                    </li>
                                    {
                                        gplist.map((item, i)=>{
                                            if (item.address === selectGP) {
                                                return <></>
                                            } else {
                                                return <li key={i}>
                                                            <div className="line">
                                                                <div className="label lft"> <span>{t('vote.GPInfoAddress')}</span>{publicJs.AddresstoShow(item.address)}
                        
                                                                </div>
                        
                                                                <div className='inputBrdr rht'>
                                                                    <div className="label">{t('vote.bonus')}</div>
                                                                    <Input
                                                                        type="number"
                                                                        placeholder={t('vote.bonus')}
                                                                        onChange={(event) => handleSetBonus(event, i)}
                                                                        autoComplete="off"/>
                                                                    <span className='suffix'>%</span>
                                                                </div>
                                                            </div>
                                                        </li>
                                            }
                                        })

                                    }
                                </ul>
                            </GpInfo>
                        }
                        
                        <VoteInfo>
                            <div className="line top">
                                <div className='label'>{t('vote.voteInfo')}</div>
                            </div>
                            <ul>
                                <li>
                                    <div className="lft">
                                        <div className="line">
                                            <div className="label">{t('vote.minimumPassed')}</div>
                                        </div>
                                        <div className='inputBrdr rht'>
                                            <Input
                                                placeholder={t('vote.enter')}
                                                onChange={e=>{setNewVoteConfig({...newVoteConfig, minSurportRequire:Number(e.target.value)})}}
                                                autoComplete="off"/>
                                        </div>
                                    </div>
                                    <div className="rht">
                                        <div className="line">
                                            <div className="label">{t('vote.minimumVotes')}</div>
                                        </div>
                                        <div className='inputBrdr rht'>
                                            <Input
                                                placeholder={t('vote.enter')}
                                                onChange={e=>{setNewVoteConfig({...newVoteConfig, minRequire:Number(e.target.value)})}}
                                                autoComplete="off"/>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </VoteInfo>
                    </div>
                </>
            }
            {
                ventureType === 5 && <ManageFeeTransfer ref={ManageFeeRef} tokenList={list} />
            }
        </Venture>
    )



    return (
        <Vote className="container font-light">
            <div className="title">
                {/* <img src={newVote} alt="" /> */}
                <span>{t('vote.new')}</span>
            </div>

            <NewVoteTop type={type}/>

            {
                type === 1 && <div>
                    <div className="line">
                        <div className="label">
                            {t('vote.EndTime')}
                            {/*<span>Select how long the vote durate by seconds</span>*/}
                        </div>
                        <DatePicker
                            className="myDatePicker"
                            placeholder={t('vote.EndTime')}
                            defaultValue={mydate}
                            style={{ width: '100%' }}
                            format="YYYY-MM-DD HH:mm:ss"
                            disabledDate={disabledDate}
                            // disabledTime={disabledDateTime}
                            allowClear={false}
                            onChange={handleChange}
                            showTime={true}
                            showNow={false} />
                    </div>
                    <div className="line">
                        <div className="label">{t('vote.Title')}</div>
                        <Input
                            placeholder={t('vote.Title')}
                            name='title'
                            value={title}
                            autoComplete="off"
                            onChange={handleInputChange}
                        />
                    </div>

                    <div className='btnLine'>
                        <Button className="previous" onClick={() => { props.history.push(pURL) }}>
                            <img className="left" src={left} alt="" />
                            {t('vote.Previous')}
                        </Button>
                        <Button className="next" type="primary" disabled={!date || !title} onClick={() => nextStep(2)}>
                            {t('vote.Next')}
                            <img src={right} alt="" />
                        </Button>
                    </div>
                </div>
            }

            {
                type === 2 && <div>
                    <div className="line">
                        <div className="label">{t('vote.mini')}</div>
                        <Input
                            placeholder={t('vote.mini')}
                            name='supportInput'
                            value={supportInput}
                            type="number"
                            onChange={(e)=>{setsupportInput(Number(e.target.value))}}
                        />
                    </div>

                    <div className="line">
                        <div className="label">{t('vote.supportPercent')}</div>
                        <Input
                            placeholder={t('vote.supportPercent')}
                            name='supportPercent'
                            value={supportPercent}
                            type="number"
                            onChange={(e)=>{setsupportPercent(Number(e.target.value))}}
                        />
                    </div>

                    <div className='btnLine'>
                        <Button className="previous" onClick={() => nextStep(1)}>
                            <img className="left" src={left} alt="" />
                            {t('vote.Previous')}
                        </Button>
                        <Button className="next" type="primary" onClick={() => nextStep(3)} >
                            {t('vote.Next')}
                            <img src={right} alt="" />
                        </Button>
                    </div>
                </div>
            }
            {
                type === 3 && <div>

                    <div className="line">
                        <div className="label">{t('vote.Description')}</div>
                        <TextArea
                            rows={4}
                            placeholder={t('vote.Description')}
                            name='desc'
                            value={desc}
                            onChange={handleInputChange}
                        />
                    </div>

                    <div className='btnLine'>
                        <Button className="previous" onClick={() => nextStep(2)}>
                            <img className="left" src={left} alt="" />
                            {t('vote.Previous')}
                        </Button>
                        <Button type="primary" onClick={() => nextStep(4)} disabled={!desc}>
                            {t('vote.Next')}
                            <img src={right} alt="" />
                        </Button>
                    </div>
                </div>
            }

            {
                type === 4 &&
                <div>
                    <div className="line">
                        <div className="label">{t('vote.Options')}</div>

                        {optionlist.map((i, index) => (
                            <div className="addLine" key={index}>
                                <div className="showOption">{i}</div>
                                {/* <Input
                                    placeholder={t('FillOption')}
                                    value={optionlist[index]}
                                    onChange={(event) => setAddress(event, index)}
                                /> */}
                                {/* {
                                    !!index && <div className="removerht" onClick={removeOption.bind(this, i, index)} >
                                        <img src={remove} alt='' />
                                    </div>
                                } */}
                            </div>
                        ))
                        }
                    </div>

                    {/* <AddButton onClick={addOption}> */}
                    {/* <img src={add} className="addRht" alt='' /> */}
                    {/* + {t('AddOption')} */}
                    {/* </AddButton> */}

                    <div className='btnLine'>
                        <Button className="previous" onClick={() => nextStep(3)}>
                            <img className="left" src={left} alt="" />
                            {t('vote.Previous')}
                        </Button>
                        <Button type="primary" onClick={() => nextStep(5)}>
                            {t('vote.Next')}
                            <img src={right} alt="" />
                        </Button>
                    </div>
                </div>
            }
            {
                type === 5 &&
                <div>
                    { isventure && VentureStep}
                    
                    {
                        !isventure && <div className="line">
                            <Checkbox onChange={(e) => handleActive(e)}>{t('vote.TokenTransfer')}</Checkbox>
                        </div>
                    }
                    {
                        optchecked && <div className="line">

                            <div className="inputBrdr">
                                <SelectBrdr style={{ width: '100%' }} onChange={handleSelect} placeholder={t('vote.SelectOption')}>
                                    {
                                        list.map((i) =>
                                            <Select.Option value={i.address} key={i.address}>
                                                <div style={{ display:'flex', alignItems:'center'}}>
                                                    <div style={{ display:'flex', flexDirection:'column', flexGrow:1}}>
                                                        <div style={{lineHeight: '3.55rem',color: '#10164B', textTransform:'uppercase'}}>{i.symbol}</div>
                                                        <div style={{color: '#A6A6B7', lineHeight: '3.55rem'}}>{`${i.address.slice(0,5)}…${i.address.slice(-5)}`}</div>
                                                    </div>
                                                    <div style={{textAlign:'right', color: '#A6A6B7', paddingRight: '2rem'}}>{t('vote.Available')}e: {i.balanceDisplay}</div>

                                                </div>
                                            </Select.Option>
                                        )
                                    }
                                </SelectBrdr>
                            </div>

                        </div>
                    }
                    {
                        optchecked && <div className="line">
                            <div className="label">{t('vote.Recipient')}</div>
                            <Input
                                placeholder={t('vote.Recipient')}
                                name='to_address'
                                value={to_address}
                                onChange={handleInputChange}
                            />
                        </div>
                    }

                    {
                        optchecked && <div className="line">
                            <div className="label">{t('vote.Amount')}</div>
                            <Input
                                placeholder={t('vote.Amount')}
                                name='valueAmount'
                                value={valueAmount}
                                onChange={handleInputChange}
                            />
                        </div>
                    }

                    <div className='btnLine'>
                        <Button className="previous" onClick={() => nextStep(4)}>
                            <img className="left" src={left} alt="" />
                            {t('vote.Previous')}
                        </Button>
                        <Button type="primary" onClick={handleClicktoVote} disabled={optchecked&&!valueAmount&&!erc20address}>
                            {t('vote.Submit')}
                            <img src={right} alt="" />
                        </Button>
                    </div>
                </div>
            }
        </Vote >
    )
}


